import React, { Component } from 'react'
import { PivotGrid, Scrolling } from 'devextreme-react/pivot-grid';
import { createStore } from 'devextreme-aspnet-data-nojquery';

const my_datasource = {
    remoteOperations: true,
    store: createStore({
        key: 'OrderID',
        loadUrl: 'https://js.devexpress.com/Demos/WidgetsGalleryDataService/api/Sales/Orders'
    }),
    fields: [{
        caption: 'Category',
        dataField: 'ProductCategoryName',
        width: 250,
        expanded: true,
        sortBySummaryField: 'SalesAmount',
        sortBySummaryPath: [],
        sortOrder: 'desc',
        area: 'row'
    }, {
        caption: 'Subcategory',
        dataField: 'ProductSubcategoryName',
        width: 250,
        sortBySummaryField: 'SalesAmount',
        sortBySummaryPath: [],
        sortOrder: 'desc',
        area: 'row'
    }, {
        caption: 'Product',
        dataField: 'ProductName',
        area: 'row',
        sortBySummaryField: 'SalesAmount',
        sortBySummaryPath: [],
        sortOrder: 'desc',
        width: 250
    }, {
        caption: 'Date',
        dataField: 'DateKey',
        dataType: 'date',
        area: 'column'
    }, {
        caption: 'Amount',
        dataField: 'SalesAmount',
        summaryType: 'sum',
        format: 'currency',
        area: 'data'
    }, {
        caption: 'Store',
        dataField: 'StoreName'
    }, {
        caption: 'Quantity',
        dataField: 'SalesQuantity',
        summaryType: 'sum'
    }, {
        caption: 'Unit Price',
        dataField: 'UnitPrice',
        format: 'currency',
        summaryType: 'sum'
    }, {
        dataField: 'Id',
        visible: false
    }]
};


export default class extends Component {
    render() {
        return (
            <div className="container">
                <h4 className="card-title">Pivotgrid</h4>
                <p className="card-text">Load data From API</p>
                <PivotGrid
                    allowSorting={true}
                    allowSortingBySummary={true}
                    allowFiltering={true}
                    height={620}
                    showBorders={true}
                    rowHeaderLayout="tree"
                    dataSource={my_datasource}>

                    <Scrolling mode="virtual" />
                </PivotGrid>
            </div>
        );
    }
}

